import Vue from 'vue';
import Vuex from 'vuex';

import state from './state';
import * as actions from './actions';
import * as getters from './getters';
import * as mutations from './mutations'

Vue.use(Vuex)

export default function() {
    const Store = new Vuex.Store({
        state,
        actions,
        getters,
        mutations
    });

    return Store;
}