export function changeShowHeader(state, showHeader) {
    state.showHeader = showHeader;
}

export function changeShowAside(state, showAside) {
    state.showAside = showAside;
}

export function changeShowFooter(state, showFooter) {
    state.showFooter = showFooter;
}

export function changeLang(state, lang) {
    state.lang = lang
}