export function changeShowHeader({ commit }, showHeader) {
    commit('changeShowHeader', showHeader)
}

export function changeShowAside({ commit }, showAside) {
    commit('changeShowAside', showAside)
}

export function changeShowFooter({ commit }, showFooter) {
    commit('changeShowFooter', showFooter)
}

export function changeLang({ commit }, lang) {
    commit('changeLang', lang)
}