<template>
	<div id="app">
		<el-container>
			<el-header class="header" v-if="showHeader">
				<div class="title"><img src="@/assets/logo.jpg" class="logo" alt=""> {{ $t("home.title") }}</div>
				<div class="nav">
					<ul>
						<li><a href="/">{{ $t("home.home") }}</a></li>
						<li><a href="">{{ $t("home.articles") }}</a></li>
						<li><a href="">{{ $t("home.resume") }}</a></li>   
						<li>
							<el-dropdown>
								<span class="dropdowntitle">GitHub <i class="el-icon-caret-bottom"></i></span>
								<el-dropdown-menu slot="dropdown">
									<el-dropdown-item>
										<a href="https://github.com/Piscoo/travel" target="_blank">特旅界</a>
									</el-dropdown-item>
									<el-dropdown-item>2</el-dropdown-item>
									<el-dropdown-item>3</el-dropdown-item>
								</el-dropdown-menu>
							</el-dropdown>
						</li>
						<li>
							<el-select v-model="lang" placeholder="切换语言" size='small' @change="changeLanguage">
								<el-option v-for="item in langArr" :key= "item.value" :label="item.label" :value="item.value"></el-option>
							</el-select>
						</li>
					</ul>
				</div>
			</el-header>
			<el-container>
				<el-aside class="aside" v-if="showAside">Aside</el-aside>
				<el-main class="main">
					<router-view />
				</el-main>
			</el-container>
				<el-footer class="footer" v-if="showFooter">Footer</el-footer>
		</el-container>
	</div>
</template>

<script>
	import { mapState } from 'vuex';
	export default {
		name: 'app',
		components: {
		},
		data() {
			return {
				langArr: [
					{ value: 'cn', label: '中文'},
					{ value: 'en', label: 'English'}
				]
			}
		},
		computed: {
			...mapState({
				showHeader: state => state.showHeader, // show header or not
				showAside: state => state.showAside,  // show sidebar or not
				showFooter: state => state.showFooter,  // show footer or not
			}),
			lang: {
				get() {
					return this.$store.state.lang
				},
				set() {}
			}
		},
		beforeCreate() {
			this.$nextTick(() => {
				this.$store.dispatch('changeShowAside', false)
				this.$store.dispatch('changeShowFooter', false)
			})
		},
		methods: {
			changeLanguage(e) {
				console.log(e)
				this.$i18n.locale = e
				this.$store.dispatch('changeLang', e)
				localStorage.setItem('lang', e)
			}
		}
	}
</script>

